'use client'

import { useEffect, useState } from 'react'
import { supabase } from '../../lib/supabaseClient'
import { useRouter } from 'next/navigation'
import StatCard from '../../components/StatCard'
import SalesBarChart from '../../components/SalesBarChart'
import RecentOrdersTable from '../../components/RecentOrdersTable'
import { DollarSign, ShoppingCart, Users, Package, Plus } from 'lucide-react'

export default function Dashboard() {
  const [user, setUser] = useState(null)
  const router = useRouter()
  const [showModal, setShowModal] = useState(false)
  const [client, setClient] = useState('')
  const [brand, setBrand] = useState('')
  const [quantity, setQuantity] = useState('')
  const [stats, setStats] = useState({
    receitaTotal: 0,
    totalPedidos: 0,
    numClientes: 0,
    numProdutos: 0
  })
  const [salesData, setSalesData] = useState([])
  const [recentOrders, setRecentOrders] = useState([])

  useEffect(() => {
    const getUser = async () => {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push('/login')
      } else {
        setUser(user)
        loadDashboardData(user.id)
      }
    }
    getUser()
  }, [router])

  const loadDashboardData = async (userId) => {
    // Buscar stats
    const { data: clients } = await supabase.from('clients').select('id').eq('user_id', userId)
    const { data: products } = await supabase.from('products').select('id').eq('user_id', userId)
    const { data: orders } = await supabase.from('orders').select('id, total_value').eq('user_id', userId)

    setStats({
      receitaTotal: orders?.reduce((sum, order) => sum + (order.total_value || 0), 0) || 0,
      totalPedidos: orders?.length || 0,
      numClientes: clients?.length || 0,
      numProdutos: products?.length || 0
    })

    // Buscar vendas por marca
    const { data: salesByBrand } = await supabase
      .from('orders')
      .select('quick_brand, total_value')
      .eq('user_id', userId)
      .eq('order_type', 'quick_brand')
      .not('quick_brand', 'is', null)

    const brandMap = {}
    salesByBrand?.forEach(order => {
      brandMap[order.quick_brand] = (brandMap[order.quick_brand] || 0) + (order.total_value || 0)
    })

    setSalesData(Object.entries(brandMap).map(([name, vendas]) => ({ name, vendas })))

    // Buscar pedidos recentes
    const { data: recent } = await supabase
      .from('orders')
      .select(`
        id,
        created_at,
        total_value,
        status,
        clients (name)
      `)
      .eq('user_id', userId)
      .order('created_at', { ascending: false })
      .limit(5)

    setRecentOrders(recent?.map(order => ({
      id: order.id,
      client: order.clients?.name || 'Cliente Desconhecido',
      total: `R$ ${order.total_value?.toFixed(2) || '0.00'}`,
      status: order.status,
      date: new Date(order.created_at).toLocaleDateString('pt-BR')
    })) || [])
  }

  const handleNewOrder = async () => {
    if (!user) return

    // Calcular total estimado (mock, ajustar conforme produtos)
    const totalValue = quantity * 100 // Exemplo: R$ 100 por unidade

    const { error } = await supabase.from('orders').insert({
      client_id: null, // Para pedido rápido, pode ser null ou buscar cliente
      status: 'Pendente',
      total_value: totalValue,
      order_type: 'quick_brand',
      quick_brand: brand,
      quick_quantity: parseInt(quantity),
      user_id: user.id
    })

    if (error) {
      alert('Erro ao criar pedido: ' + error.message)
    } else {
      alert('Pedido criado com sucesso!')
      setShowModal(false)
      setClient('')
      setBrand('')
      setQuantity('')
      loadDashboardData(user.id) // Recarregar dados
    }
  }

  if (!user) {
    return <div>Carregando...</div>
  }

  const statCards = [
    { title: 'Receita Total', value: `R$ ${stats.receitaTotal.toFixed(2)}`, icon: <DollarSign className="h-6 w-6 text-gray-400" /> },
    { title: 'Total de Pedidos', value: stats.totalPedidos.toString(), icon: <ShoppingCart className="h-6 w-6 text-gray-400" /> },
    { title: 'Nº de Clientes', value: stats.numClientes.toString(), icon: <Users className="h-6 w-6 text-gray-400" /> },
    { title: 'Nº de Produtos', value: stats.numProdutos.toString(), icon: <Package className="h-6 w-6 text-gray-400" /> },
  ]

  return (
    <div className="min-h-screen bg-gray-100">
      <header className="bg-white shadow">
        <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8 flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Dashboard do Representante</h1>
            <p>Bem-vindo, {user.email}</p>
          </div>
          <button
            onClick={() => setShowModal(true)}
            className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700"
          >
            <Plus className="h-5 w-5 mr-2" />
            Novo Pedido Rápido
          </button>
        </div>
      </header>
      <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
        <div className="px-4 py-6 sm:px-0">
          <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4 mb-8">
            {statCards.map((stat, index) => (
              <StatCard key={index} title={stat.title} value={stat.value} icon={stat.icon} />
            ))}
          </div>
          <div className="mb-8">
            <SalesBarChart data={salesData} />
          </div>
          <div>
            <RecentOrdersTable orders={recentOrders} />
          </div>
        </div>
      </main>

      {showModal && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full" onClick={() => setShowModal(false)}>
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white" onClick={(e) => e.stopPropagation()}>
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">Novo Pedido Rápido</h3>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">Cliente (Opcional)</label>
                  <input
                    type="text"
                    value={client}
                    onChange={(e) => setClient(e.target.value)}
                    className="mt-1 block w-full border-gray-300 rounded-md shadow-sm"
                    placeholder="Nome do cliente"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">Marca</label>
                  <input
                    type="text"
                    value={brand}
                    onChange={(e) => setBrand(e.target.value)}
                    className="mt-1 block w-full border-gray-300 rounded-md shadow-sm"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">Quantidade</label>
                  <input
                    type="number"
                    value={quantity}
                    onChange={(e) => setQuantity(e.target.value)}
                    className="mt-1 block w-full border-gray-300 rounded-md shadow-sm"
                    required
                  />
                </div>
              </div>
              <div className="flex justify-end mt-4">
                <button
                  onClick={() => setShowModal(false)}
                  className="mr-2 px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 border border-gray-300 rounded-md hover:bg-gray-200"
                >
                  Cancelar
                </button>
                <button
                  onClick={handleNewOrder}
                  className="px-4 py-2 text-sm font-medium text-white bg-indigo-600 border border-transparent rounded-md hover:bg-indigo-700"
                >
                  Criar Pedido
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}